$bg-color: rgb(43, 43, 43);
$primary-color: rgb(29, 29, 29);
$secondary-color: rgb(39, 39, 39);

$team-member-empty: rgb(95, 93, 93);

@mixin center-horizontally {
    position: relative;
    left:50%;
    transform: translateX(-50%);
}
@mixin center-vertically {
    position: relative;
    top:50%;
    transform: translateY(-50%);
}